import SoomtutNavbar from "../components/SoomtutNavbar";
import HomeContents from "../components/HomeContents";

function Home() {
    const [View, token, member] = SoomtutNavbar();

    return (
        <div>
            <div> {process.env.REACT_APP_HOST} </div>
         <View />
         <HomeContents />
        </div>
    );
}

export default Home;